import React from "react"
import { graphql } from "gatsby"
import Img from "gatsby-image"

import Card from "react-bootstrap/Card"

import Layout from "../components/layout"
import SEO from "../components/seo"

const BlogsPage = ({ data }) => (
  <Layout>
    <SEO title="Blogs" />
    <div>
      <h1
        style={{
          display: `inline-block`,
          borderBottom: `4px solid`,
          fontFamily: `IM Fell DW Pica`,
          fontSize: `301%`,
        }}
      >
        Blogs
      </h1>
      <h4
        style={{
          fontFamily: `Gotu`,
        }}
      >
        {data.allMdx.totalCount} Posts
      </h4>
      {data.allMdx.edges.map(({ node }) => (
        <div key={node.id}>
          <Card style={{ boxShadow: `8px 8px 5px grey` }}>
            <Card.Body>
              <Card.Link
                style={{
                  all: `inherit`,
                  cursor: `pointer`,
                }}
                href={node.fields.slug}
              >
                <Card.Title
                  style={{
                    marginBottom: `0.25`,
                    font: `italic bold 200% Merriweather`,
                  }}
                >
                  {node.frontmatter.title}
                  <p
                    style={{
                      font: `normal normal 40% Arial`,
                    }}
                  >
                    {node.frontmatter.date} &#183; {node.timeToRead} min read
                  </p>
                </Card.Title>
                <Img fluid={node.frontmatter.image.childImageSharp.fluid} />
                <Card.Text
                  style={{
                    marginTop: `2.25`,
                    font: `italic normal 120% Fira Code`,
                  }}
                >
                  {node.excerpt}
                </Card.Text>
              </Card.Link>
            </Card.Body>
          </Card>
          <br></br>
        </div>
      ))}
    </div>
  </Layout>
)

export default BlogsPage

export const query = graphql`
  query {
    allMdx(
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { fields: { slug: { regex: "/blogs/" } } }
    ) {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            author
            image {
              childImageSharp {
                fluid(maxWidth: 800, maxHeight: 250, quality: 100, fit: COVER) {
                  ...GatsbyImageSharpFluid_withWebp
                }
              }
            }
          }
          headings {
            value
            depth
          }
          fields {
            slug
          }
          excerpt
          timeToRead
        }
      }
    }
  }
`
